@import './header';
@import './cards';
@import './stars';

* {
  margin: 0;
}

.container {
  justify-content: center;
  align-items: center;
  padding: 50px 40px;
  display: grid;
  gap: 46px 48px;

  --column-count: 1;

  grid-template-columns: repeat(var(--column-count), 200px);

  @media (min-width: 488px) {
    --column-count: 2;
  }

  @media (min-width: 768px) {
    --column-count: 3;
  }

  @media (min-width: 1024px) {
    --column-count: 4;
  }
}
